//=====INTRO=========
.quiz-intro {

    background-image: url(../img/intro-bg.jpg);
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    min-height: 100vh;

    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: rgba(18, 18, 17, 0.9);
    }

    
    &__wrap {
        padding-bottom: 70px;
    }

    &__header {
        padding: 60px 0 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 130px;
        position: relative;
        z-index: 1;
    }

    &__logo {
        transform: translateY(-50px);
        opacity: 0;
        animation: slideDown 0.7s forwards ease-out;
        animation-delay: 1s;
    }

    &__link {
        font-size: 21px;
        line-height: 145%;
        color: #808080;
        opacity: 0.85;

        transform: translateY(-50px);
        opacity: 0;
        animation: slideDown 0.7s forwards ease-out;
        animation-delay: 1.2s;
    }

    

    &__title {
        font-size: 64px;
        line-height: 100%;
        font-weight: 700;
        color: #fbfbfb;
        max-width: 668px;
        position: relative;
        z-index: 1;

        transform: translateY(-50px);
        opacity: 0;
        animation: slideDown 0.7s forwards ease-out;
        animation-delay: 1.4s;
    }

    &__subtitle {
        font-size: 38px;
        line-height: 150%;
        font-weight: 700;
        color: #fbfbfb;        
        max-width: 658px;
        position: relative;
        z-index: 1;
        display: inline-block;
        padding-right: 40px;
        margin-bottom: 100px;

        transform: translateY(-50px);
        opacity: 0;
        animation: slideDown 0.7s forwards ease-out;
        animation-delay: 1.6s;

        &:after {
            content: '';
            position: absolute;
            right: 0;
            top: 0;
            bottom: 0;
            margin: auto;
            background-image: url(../img/icons/title-placemark.svg);
            width: 26px;
            height: 36px;
        }
    }

    &__text {
        position: relative;
        max-width: 400px;
        z-index: 1;
        color: $white;
        font-size: 21px;
        line-height: 140%;
        margin-bottom: 40px;

        transform: translateY(-50px);
        opacity: 0;
        animation: slideDown 0.7s forwards ease-out;
        animation-delay: 1.8s;
    }

    &__btn {
        display: inline-block;
        padding: 17px 65px;
        position: relative;
        z-index: 1;
        font-size: 21px;
        line-height: 96%;
        color: $white;
        text-transform: lowercase;
        border: 2px solid #7A7A7A;

        transform: translateY(-50px);
        opacity: 0;
        animation: slideDown 0.7s forwards ease-out;
        animation-delay: 2s;
    }
    
    &__advantages {
        display: none;
        
        transition: opacity 0.3s;
        opacity: 0;
        position: relative;
        z-index: 1;
        
        &.visible {
            display: block;
            transform: translateY(-50px);
            opacity: 0;
            animation: slideDown 0.7s forwards ease-out;
            margin-bottom: 70px;
        }
    }

    &__row {
        display: flex;
        position: relative;
        z-index: 1;
        margin-top: 250px;

        transform: translateY(-50px);
        opacity: 0;
        animation: slideDown 0.7s forwards ease-out;
        animation-delay: 2.2s;

        &_mobile {
            display: block;
            max-width: 388px;
            margin-left: auto;
            margin-right: auto;
            margin-top: 0;

            .slick-list {
                overflow: visible;
            }

            .slick-slide {
                max-width: 230px;
                position: relative;
                transition: 0.3s;
                outline: none;
                box-shadow: 6px 4px 4px rgba(0, 0, 0, 0.43);

                &:nth-of-type(1) {
                    z-index: 5;
                    transform: translateX(0);
                    filter: blur(0);
                }

                &:nth-of-type(2) {
                    transform: translateX(-178px) scale(0.9);
                    z-index: 4;
                    filter: blur(2px);
                }

                &:nth-of-type(3) {
                    transform: translateX(-356px) scale(0.8);
                    z-index: 3;
                    filter: blur(2px);
                }

                &:nth-of-type(4) {
                    transform: translateX(-534px) scale(0.7);
                    z-index: 2;
                    filter: blur(3px);
                }

                &:nth-of-type(5) {
                    transform: translateX(-712px) scale(0.6);
                    z-index: 1;
                    filter: blur(3px);
                }

                &.hidden {
                    opacity: 0;
                }

                &.first {
                    z-index: 5;
                    transform: translateX(0);
                    filter: blur(0);
                }

                &.second {
                    transform: translateX(-178px) scale(0.9);
                    z-index: 4;
                    filter: blur(2px);
                }
                &.third {
                    transform: translateX(-356px) scale(0.8);
                    z-index: 3;
                    filter: blur(2px);
                }
                &.fourth {
                    transform: translateX(-534px) scale(0.7);
                    z-index: 2;
                    filter: blur(3px);
                }
                &.fifth {
                    transform: translateX(-712px) scale(0.6);
                    z-index: 1;
                    filter: blur(3px);
                }
            }

            .quiz-intro__row__item { 
                flex-basis: auto;
                border: none;
                height: 360px;
                padding: 15px;
                position: relative;
                background-size: cover;
                background-position: center center;
                margin: 0;
                

                &:before {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: 0;
                    width: 100%;
                    height: 100%;
                    background-color: rgba(60, 60, 60, 0.75);
                }

                &_one {
                    background-image: url(../img/intro-item-bg1.jpg);
                }
                &_two {
                    background-image: url(../img/intro-item-bg2.jpg);
                }
                &_three {
                    background-image: url(../img/intro-item-bg3.jpg);
                }
                &_four {
                    background-image: url(../img/intro-item-bg4.jpg);
                }
                &_five {
                    background-image: url(../img/intro-item-bg5.jpg);
                }
            }

            .quiz-intro__row__item__number {
                font-size: 136px;
                line-height: 96%;
                color: rgba(255, 255, 255, 0.5);
                font-weight: 700;
                position: relative;
                z-index: 2;
                height: auto;

                &:after {
                    display: none;
                }
            }

            .quiz-intro__row__item__text {
                padding-top: 0;
                font-size: 21px;
                line-height: 96%;
                font-weight: 700;
                color: $white;
                transition: 0.3s;
                position: relative;
                z-index: 2;
                display: flex;
                align-items: flex-end;
                height: 145px;
                margin: 16px -15px;
                padding: 0 15px 15px;
                background: linear-gradient(0deg, rgba(32, 29, 26, 0.3723) 8.21%, rgba(33, 31, 28, 0) 100%);

                &:before {
                    display: none;
                }
            }
        }
    }

    &__row__item {        
        flex-basis: calc(100% / 5);      
        border-left: 2px solid #474747;
        padding: 40px 20px;
        
        &:last-of-type {
            border-right: 2px solid #474747;
        }

        &:hover {
            .quiz-intro__row__item__number {
                &:before {
                    opacity: 1;
                }

                &:after {
                    box-shadow: 0px 0px 5px rgba(241, 165, 17, 0.24), 0px 0px 7px rgba(255, 255, 255, 0.72);

                }
            }

            .quiz-intro__row__item__text {
                text-shadow: 0px 0px 5px rgba(241, 165, 17, 0.24), 0px 0px 7px rgba(255, 255, 255, 0.72);
            }
        }
    }

    &__row__item__number {
        display: block;
        position: relative;
        padding-bottom: 54px;
        height: 117px;
        transition: 0.3s;

        &_one {
            &:before {
                width: 59px;
                height: 102px;
                background-image: url(../img/intro-item1-hover.png);
            }
        }

        &_two {
            &:before {
                width: 81px;
                height: 104px;
                background-image: url(../img/intro-item2-hover.png);
            }
        }

        &_three {
            &:before {
                width: 84px;
                height: 106px;
                background-image: url(../img/intro-item3-hover.png);
            }
        }

        &_four {
            &:before {
                width: 89px;
                height: 102px;
                background-image: url(../img/intro-item4-hover.png);
            }
        }

        &_five {
            &:before {
                width: 85px;
                height: 104px;
                background-image: url(../img/intro-item5-hover.png);
            }
        }

        &:before {
            content: '';
            position: absolute;
            top: -7px;
            left: -7px;
            opacity: 0;
            transition: 0.3s;
        }

        &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 2px;
            background-color: #5B5656;
            transition: 0.3s;
        }
    }

    &__row__item__text {
        padding-top: 110px;
        font-size: 21px;
        line-height: 96%;
        color: rgba(245, 245, 245, 0.87);
        transition: 0.3s;
    }

    &__bottom {
        position: absolute;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        z-index: 1;
    }
    
    &__about-btn {
        display: none;
        font-size: 20px;
        line-height: 24px;        
        color: #C5C4C4;
        text-align: center;
        padding: 10px 0;
        span {
            display: block;
            
        }

        &__icon {
            position: relative;
            display: block;
            width: 32px;
            height: 32px;
            border-radius: 50px;
            border: 2px solid #C5C4C4;       
            text-align: center;
            line-height: 36px;            
            margin: 8px auto 0;
        }
    }

    &__bottom__phone {
        display: none;
        width: 31px;
        height: 35px;
        margin: 80px auto 44px;
        
        &.visible {
            display: block!important;
        }
    }

    &__bottom__link {
        display: none;
        margin: 0 auto;
        text-align: center;
        max-width: 160px;
        font-size: 21px;
        line-height: 96%;        
        color: rgba(255, 255, 255, 0.91);

        &.visible {
            display: block!important;
        }
    }

}

@media screen and (max-width: 1440px) {
    .quiz-intro {
        &__header {        
            margin-bottom: 100px;
        }

        &__title {
            font-size: 45px;      
            max-width: 668px;
        }
    
        &__subtitle {
            font-size: 30px;     
            margin-bottom: 50px;    
        }
    
        &__text {            
            font-size: 18px;
            line-height: 140%;
            margin-bottom: 30px;
        }
    
        &__btn {            
            font-size: 18px;
            line-height: 96%;
        
        }

        &__row {
            margin-top: 100px;

            &_mobile {
                margin-top: 0;
            }
        }

        &__row__item__text {
            padding-top: 50px;
            font-size: 18px;
            
        }
    }    
}

@media screen and (max-width: 1200px) {
    .quiz-intro {
        &__row {
            flex-wrap: wrap;
        }
        &__row__item {        
            flex-basis: calc(100% / 3);      
            border-left: 2px solid #474747;
            padding: 40px 20px;
            margin-bottom: 35px;
            
            &:last-of-type {
                border-right: none;
            }
    
            &:hover {
                .quiz-intro__row__item__number {
                    &:before {
                        opacity: 0;
                    }
    
                    &:after {
                        box-shadow: none;
    
                    }
                }
    
                .quiz-intro__row__item__text {
                    text-shadow: none;
                }
            }
        }
    }
}

@media screen and (max-width: 992px) {
    .quiz-intro {
        &__logo {
            animation: none;
            transform: translateY(0);
            opacity: 1;
        }

        &__link {
            animation: none;
            transform: translateY(0);
            opacity: 1;
        }
    
        &__title {
            animation: none;
            transform: translateY(0);
            opacity: 1;
        }
    
        &__subtitle {
            animation: none;
            transform: translateY(0);
            opacity: 1;
        }
    
        &__text {
            animation: none;
            transform: translateY(0);
            opacity: 1;
        }
    
        &__btn {
            animation: none;
            transform: translateY(0);
            opacity: 1;
        }        
    
        &__row {
            animation: none;
            transform: translateY(0);
            opacity: 1;
        }
    }
}

@media screen and (max-width: 768px) {
    .quiz-intro {
        
        &__row__item {        
            flex-basis: calc(100% / 2);            
        }
    }
}

@media screen and (max-width: 576px) {
    .quiz-intro {
        &__wrap {
            padding-bottom: 80px;
            min-height: 100vh;
        }
        &__header {   
            padding: 36px 0 0;     
            margin-bottom: 10vh;
        }

        &__link {
            display: none;
        }

        &__content {
            &.hiding {
                animation: slideOut 0.7s forwards linear;
            }
            &.hidden {
                display: none;
            }
        }

        &__title {
            font-size: 30px;
            max-width: 668px;
        }
    
        &__subtitle {
            font-size: 18px;
            line-height: 200%;    
            margin-bottom: 4vh;    
        }
    
        &__text {            
            font-size: 14px;
            line-height: 140%;
            margin-bottom: 4vh;
        }

        &__btn {
            display: block;
            padding: 17px;
            text-align: center;
        }

        &__about-btn {
            display: block;
        }

        &__row {
            display: none;

            &_mobile {
                display: block;
            }
        }
        
        &__row__item {        
            flex-basis: calc(100% / 2);            
        }
    }
}

@media screen and (max-width: 460px) {
    .quiz-intro {
        &__row {
            &_mobile {
                max-width: 388px;
    
                .slick-slide {
                    max-width: 210px;
    
                    &:nth-of-type(1) {
                        
                        transform: translateX(0);
                        
                    }
    
                    &:nth-of-type(2) {
                        transform: translateX(-173px) scale(0.9);
                       
                    }
    
                    &:nth-of-type(3) {
                        transform: translateX(-346px) scale(0.8);
                        
                    }
    
                    &:nth-of-type(4) {
                        transform: translateX(-519px) scale(0.7);
                        
                    }
    
                    &:nth-of-type(5) {
                        transform: translateX(-692px) scale(0.6);
                       
                    }
    
                    &.first {
                        
                        transform: translateX(0);
                        
                    }
    
                    &.second {
                        transform: translateX(-173px) scale(0.9);
                        
                    }

                    &.third {
                        transform: translateX(-346px) scale(0.8);
                        
                    }
                    &.fourth {
                        transform: translateX(-519px) scale(0.7);
                        
                    }
                    &.fifth {
                        transform: translateX(-692px) scale(0.6);
                        
                    }
                }
                .quiz-intro__row__item { 
                    height: 280px;
                }
                .quiz-intro__row__item__text {
                    height: 120px;
                    font-size: 18px;
                }
            }
        }
        &__row__item__number {
            display: block;
            position: relative;
            padding-bottom: 0;
            height: 117px;
            transition: 0.3s;

        }
    }
}