.quiz-thanks {

    background-image: url(../img/intro-bg.jpg);
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    min-height: 100vh;

    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: rgba(18, 18, 17, 0.9);
    }

    
    &__wrap {
        
        position: relative;
        z-index: 1;
        min-height: 100vh;
    }

    &__header {
        padding: 60px 0 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 0;
        position: relative;
        z-index: 1;
    }

    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        min-height: calc(100vh - 143px);
    }

    &__title {
        color: #FBFBFB;
        font-size: 38px;
        line-height: 100%;
        font-weight: 700;
    }
    &__text {
        color: #FBFBFB;
        margin-top: 20px;
        margin-bottom: 20px;
        text-align: center;
    }
    
}

@media screen and (max-width: 576px) {
    .quiz-thanks {
        &__header {   
            padding: 36px 0 30px;                 
        }

        &__content {
            min-height: calc(100vh - 119px);
        }
    }
}