//=========STEPS============
.quiz {   
    

    &__wrap {
        background-size: cover;
        background-position: center center;
        min-height: 100vh;
        position: relative;

        &:after {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background-color: rgb(18, 18, 17);
            opacity: 0.65;
            transition: 0.3s;
        }

        &.change {
            transition: 0.3s;
            &:after {
                opacity: 1;
                transition: 0.3s;
            }
            
        }

        &.last-steps {
            .quiz__footer-next {
                display: none;
            }
        }
    }

    &__content {
        width: 100%;
        max-width: 826px;
        min-height: 100vh;
        background: rgba(18, 18, 17, 0.75);
        position: relative;
        z-index: 1;
        padding: 0px 134px 150px 90px;
    }


    &__header {
        padding: 40px 0 25px;
        margin-bottom: 5vh;
        
        transform: translateY(-50px);
        opacity: 0;
        animation: slideDown 0.7s forwards ease-out;
        animation-delay: 0.8s;
    }
    
    &__form-step {
        display: none;

        &_offer {
            .quiz__form-step-title {
                margin-bottom: 10px;
                margin-top: 15vh;
                font-size: 40px;
                line-height: 100%;
            }

            .quiz__form-step-subtitle {
                margin-bottom: 40px;
            }

            .quiz__form-btn {
                border: 2px solid #B9B9B9;
                font-size: 24px;
                line-height: 96%;                
                text-align: center;                
                color: #B5B5B5;
                display: block;
                max-width: 376px;
                padding: 10px;
                margin-bottom: 0;
            }
        }

        &_callback {
            .quiz__form-step-title {
                margin-bottom: 30px;
                margin-top: 12vh;
                font-size: 51px;
                line-height: 100%;
            }

            .quiz__form-step-subtitle {
                margin-bottom: 6vh;
                font-size: 21px;
                line-height: 100%;
            }

            .quiz__form-btn {
                border: 2px solid #B9B9B9;
                font-size: 24px;
                line-height: 96%;                
                text-align: center;                
                color: #B5B5B5;
                display: block;
                max-width: 376px;
                padding: 10px;
                margin-bottom: 36px;
            }
        }
    }

    &__form-step--active {
        display: block;

        transform: translateY(-50px);
        opacity: 0;
        animation: slideDown 0.7s forwards ease-out;
        animation-delay: 1s;
    }

    &__form-step-title {
        max-width: 500px;
        font-size: 38px;
        line-height: 100%;
        font-weight: 700;
        margin-bottom: 5vh;
        color: #FBFBFB;
    }   

    &__form-step-subtitle {
        color: #FBFBFB;
        font-weight: 400;

        &_offer {
            font-size: 30px;
            line-height: 100%;
        }

        &_callback {
            font-size: 21px;
            line-height: 100%;
        }
    }

    &__form-step-content {
        
    }

    &__form-label {
        display: block;
        margin-bottom: 30px;
        max-width: 588px;

        &:nth-last-of-type(1) {
            margin-bottom: 0;
        }
    }

    &__form-name {
        font-size: 21px;
        line-height: 100%;
        color: #FBFBFB;
        display: block;
        font-weight: 400;
    }

    &__form-input {
        background: none;
        border: none;
        border-bottom: 2px solid rgba(247, 247, 247, 0.35);
        padding: 14px 0;
        font-size: 19px;
        line-height: 145%;
        color: #B6B6B6;
        width: 100%;
        text-align: left;
        font-family: $mazzard;
    }

    &__form-textarea {
        padding: 20px;
        border: 2px solid #7A7A7A;
        background: none;
        width: 100%;
        height: 270px;
        color: $white;
        font-size: 21px;
        line-height: 96%;
    }

    &__form-checkbox {
        position: absolute;
        z-index: -1;
        opacity: 0;
        margin: 10px 0 0 20px;

        &:checked + .quiz__form-checkbox-text {
            box-shadow: 0px 0px 7px 0px #F49033,
                        inset 0px 0px 7px 0px #F49033;
            text-shadow: 0px 0px 5px #F49033;


            &:after {
                content: '';
                position: absolute;
                z-index: 2;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                box-shadow: 0px 0px 2px 0px rgba($color: $white, $alpha: 0.53),
                            inset 0px 0px 2px 0px rgba($color: $white, $alpha: 0.53);
            }
                        
        }
    }

    &__form-checkbox-text {
        font-size: 21px;
        line-height: 96%;
        color: $white;
        cursor: pointer;
        display: block;
        border: 2px solid #7A7A7A;
        width: 100%;
        max-width: 588px;
        transition: 0.2s;
        padding: 12px;
        text-align: center;
        position: relative;
        z-index: 1;

        &:after {
            content: '';
            position: absolute;
            z-index: 2;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            transition: 0.3s;
        }

        &:hover {
            box-shadow: 0px 0px 7px 0px #F49033,
                        inset 0px 0px 7px 0px #F49033;
            text-shadow: 0px 0px 5px #F49033;


            &:after {
                content: '';
                position: absolute;
                z-index: 2;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                box-shadow: 0px 0px 2px 0px rgba($color: $white, $alpha: 0.53),
                            inset 0px 0px 2px 0px rgba($color: $white, $alpha: 0.53);
            }            
        } 
    }


    &__form-radio {
        position: absolute;
        z-index: -1;
        opacity: 0;
        margin: 10px 0 0 20px;

        &:checked + .quiz__form-radio-text {
            box-shadow: 0px 0px 7px 0px #F49033,
                        inset 0px 0px 7px 0px #F49033;
            text-shadow: 0px 0px 5px #F49033;


            &:after {
                content: '';
                position: absolute;
                z-index: 2;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                box-shadow: 0px 0px 2px 0px rgba($color: $white, $alpha: 0.53),
                            inset 0px 0px 2px 0px rgba($color: $white, $alpha: 0.53);
            }
                        
        }
    }

    &__form-radio-text {
        font-size: 21px;
        line-height: 96%;
        color: $white;
        text-transform: lowercase;
        cursor: pointer;
        display: block;
        border: 2px solid #B9B9B9;
        width: 100%;
        max-width: 466px;
        transition: 0.2s;
        padding: 15px 12px;
        text-align: center;
        position: relative;
        z-index: 1;
        margin: 0 auto;
        color: #B5B5B5;
        

        &:hover {
        box-shadow: 0px 0px 7px 0px #F49033,
                        inset 0px 0px 7px 0px #F49033;
        text-shadow: 0px 0px 5px #F49033;


            &:after {
                content: '';
                position: absolute;
                z-index: 2;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                box-shadow: 0px 0px 2px 0px rgba($color: $white, $alpha: 0.53),
                            inset 0px 0px 2px 0px rgba($color: $white, $alpha: 0.53);
            }  

                      
        } 
    }

    &__form-submit {
        font-family: $mazzard;
        width: 100%;
        max-width: 588px;
        height: 46px;
        text-align: center;
        font-size: 21px;
        line-height: 96%;
        color: #B5B5B5;
        background-color: rgba(255, 255, 255, 0.18);
        border: none;
        margin-top: 30px;
        cursor: pointer;
    }

    &__form-agree {
        display: block;
        max-width: 588px;
        color: #FBFBFB;
        font-size: 12px;
        line-height: 100%;
        text-align: center;
        margin-top: 20px;
    } 

    &__footer {
        display: flex;
        justify-content: space-between;
        position: absolute;
        left: 90px;
        bottom: 57px;
        width: 100%;
        max-width: 588px;
        z-index: 2;

        transform: translateY(-50px);
        opacity: 0;
        animation: slideDown 0.7s forwards ease-out;
        animation-delay: 1.2s;
    }


    &__footer-btns {
        font-size: 0;
        display: flex;
        align-items: center;        
        width: 100%;
    }

    &__footer-prev {
        display: block;
        opacity: 0;
        font-size: 24px;
        line-height: 96%;
        color: $white;
        text-align: center;        
        margin-right: 30px;
        cursor: pointer;
        transition: 0.3s;
        position: relative;

        &.visible {
            display: block;
            opacity: 1;
            

            &:after {
                width: 600px;
                transition-delay: 1s;
            }
        }

        &:after {
            content: '';
            position: absolute;
            right: -10px;
            bottom: -10px;
            height: 2px;
            width: 0;
            background-color: $white;
            transition: 1s;
        }
        &:hover {
            &:after {
                width: 97px;
                
            }
        }
        
        
    }

    &__footer-next {
        font-family: $mazzard;
        font-size: 24px;
        line-height: 96%;
        color: #1C1916;
        background-color: rgba(255, 255, 255, 0.35);                
        padding: 12px;
        display: block;
        width: 338px;
        max-width: 338px;        
        cursor: pointer;
        transition: 0.3s;
        margin-left: auto;
        margin-right: 0;
        text-align: center;
        &:hover {
            
        }
    
    }

    &__advantages {
        position: absolute;
        right: 0;
        top: 0;
        width: calc(100% - 826px);
        height: 100%;
        padding: 70px 35px 40px;
        z-index: 2;
        display: none;

        

        &.active {
            display: block;

            .quiz__advantages__item {
                transform: translateY(-50px);
                opacity: 0;
                animation: slideDown 0.7s forwards ease-out;

                &:nth-of-type(1) {
                    animation-delay: 1.1s;
                    
                }
        
                &:nth-of-type(2) {
                    animation-delay: 1.2s;
                }
                
                &:nth-of-type(3) {
                    animation-delay: 1.3s;
                }
        
                &:nth-of-type(4) {
                    animation-delay: 1.4s;
                }
        
                &:nth-of-type(5) {
                    animation-delay: 1.5s;
                }
            }
            
        }
    }

    &__advantages__item {
        position: absolute;
        background: rgba(15, 15, 15, 0.5);
        padding: 40px 30px;
        width: 233px;
        filter: blur(3px);
        transition: 0.3s;
    

        &:hover {
            filter: blur(0);
            transform: scale(1.05);
            z-index: 3;
        }
        
        
        
        &:nth-of-type(1) {
            left: 35px;
            right: 500px;
            top: 8vh;
            margin: auto;
            
        }

        &:nth-of-type(2) {
            right: 35px;
            left: 450px;
            top: 22vh;
            margin: auto;
        }
        
        &:nth-of-type(3) {
            left: 35px;
            top: 20vh;
            bottom: 0;
            height: 342px;
            right: 145px;
            margin: auto;
        }

        &:nth-of-type(4) {
            right: 35px;
            left: 400px;
            bottom: 80px;
            margin: auto;
        }

        &:nth-of-type(5) {
            left: 35px;
            right: 450px;
            top: auto;
            bottom: 40px;
            margin: auto;
        }
    }

    &__advantages__item__number {
        text-align: right;
        padding-bottom: 20px;
        height: 117px;
        transition: 0.3s;
        position: relative;

        &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 2px;
            background-color: #A3A3A3;
            transition: 0.3s;
        }
    }

    &__advantages__item__text {
        font-size: 18px;
        
        line-height: 96%;
        color: rgba(245, 245, 245, 0.87);
        padding-top: 60px;
    }   
    
    &__advantages_mobile {
        display: none;
        position: static;
        padding: 0;
        width: 100%;
        max-width: 500px;
        margin-top: 40px;
    
        .slick-list {
            overflow: visible;
        }

        .slick-slide {
            max-width: 230px;
            position: relative;
            transition: 0.3s;
            outline: none;
            box-shadow: 6px 4px 4px rgba(0, 0, 0, 0.43);

            &:nth-of-type(1) {
                z-index: 5;
                transform: translateX(0);
                filter: blur(0);
            }

            &:nth-of-type(2) {
                transform: translateX(-155px) scale(0.9);
                z-index: 4;
                filter: blur(2px);
            }

            &:nth-of-type(3) {
                transform: translateX(-310px) scale(0.8);
                z-index: 3;
                filter: blur(2px);
            }

            &:nth-of-type(4) {
                transform: translateX(-465px) scale(0.7);
                z-index: 2;
                filter: blur(3px);
            }

            &:nth-of-type(5) {
                transform: translateX(-620px) scale(0.6);
                z-index: 1;
                filter: blur(3px);
            }

            &.hidden {
                opacity: 0;
            }

            &.first {
                z-index: 5;
                transform: translateX(0);
                filter: blur(0);
            }

            &.second {
                transform: translateX(-155px) scale(0.9);
                z-index: 4;
                filter: blur(2px);
            }
            &.third {
                transform: translateX(-310px) scale(0.8);
                z-index: 3;
                filter: blur(2px);
            }
            &.fourth {
                transform: translateX(-465px) scale(0.7);
                z-index: 2;
                filter: blur(3px);
            }
            &.fifth {
                transform: translateX(-620px) scale(0.6);
                z-index: 1;
                filter: blur(3px);
            }
        }

        .quiz__advantages__item {
            transform: translateY(0);
            opacity: 1;
            animation: none;
            filter: blur(0);
            border: none;
            height: 360px;
            padding: 15px;
            position: relative;
            left: auto;
            top: auto;
            right: auto;
            bottom: auto;
            background-size: cover;
            background-position: center center;
            margin: 0;
            

            &:before {
                content: '';
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(60, 60, 60, 0.75);
            }

            &_one {
                background-image: url(../img/intro-item-bg1.jpg);
            }
            &_two {
                background-image: url(../img/intro-item-bg2.jpg);
            }
            &_three {
                background-image: url(../img/intro-item-bg3.jpg);
            }
            &_four {
                background-image: url(../img/intro-item-bg4.jpg);
            }
            &_five {
                background-image: url(../img/intro-item-bg5.jpg);
            }
        }

        .quiz__advantages__item__number {
            font-size: 136px;
            line-height: 96%;
            color: rgba(255, 255, 255, 0.5);
            font-weight: 700;
            position: relative;
            z-index: 2;
            height: auto;
            padding-bottom: 55px;

            &:after {
                display: none;
            }
        }

        .quiz__advantages__item__text {
            padding-top: 0;
            font-size: 21px;
            line-height: 96%;
            font-weight: 700;
            color: $white;
            transition: 0.3s;
            position: relative;
            z-index: 2;
            display: flex;
            align-items: flex-end;
            height: 145px;
            margin: 16px -15px;
            padding: 0 15px 15px;
            background: linear-gradient(0deg, rgba(32, 29, 26, 0.3723) 8.21%, rgba(33, 31, 28, 0) 100%);

            &:before {
                display: none;
            }
        }
    }
}

@media screen and (max-width: 1440px) {
    .quiz {

        &__wrap {
            &.change {
                transition: 0.3s;
                &:after {
                    opacity: 1;
                    transition: 0.3s;
                }
                
            }
        }

        &__form-step {
            
    
            &_offer {
                

                .quiz__form-btn {
                    margin-bottom: 0;
                }
            }

            &_callback {
                .quiz__form-step-title {
                   
                    margin-top: 0;
                }
    
                

                .quiz__form-btn {
                    margin-bottom: 36px;
                }
            }
        }

        &__advantages {           
    
            &.active {
                display: none;
            }
        }

        &__advantages_mobile {
            display: block;
        }
    }
}

@media screen and (max-width: 1200px) {
    .quiz {
        &__header {
            padding: 36px 0 25px;
        }

        &__content {
            padding: 0px 134px 120px 90px;
        }

        &__form-step {
            &_offer {
                .quiz__form-step-title {
                    margin-top: 0;
                    font-size: 36px;
                }
    
                .quiz__form-step-subtitle {
                    margin-bottom: 10vh;
                    font-size: 28px;
                }
    
                .quiz__form-btn {                   
                    font-size: 21px;
                }
            }
    
            &_callback {
                .quiz__form-step-title {
                    margin-bottom: 30px;
                    margin-top: 0;
                    font-size: 36px;
                    line-height: 100%;
                }
    
                .quiz__form-step-subtitle {
                    margin-bottom: 6vh;
                    font-size: 21px;
                    line-height: 100%;
                }
    
                .quiz__form-btn {
                    border: 2px solid #B9B9B9;
                    font-size: 24px;
                    line-height: 96%;                
                    text-align: center;                
                    color: #B5B5B5;
                    display: block;
                    max-width: 376px;
                    padding: 10px;
                    margin-bottom: 36px;
                }
            }
        }

        &__form-step-title {            
            font-size: 30px;         
        }

        &__form-name {
            font-size: 18px;
        }

        &__form-input {
            font-size: 16px;
        }

        &__form-checkbox-text {
            font-size: 18px;
            padding: 10px;
        }

        &__form-submit {
            font-size: 18px;
        }

        &__footer {
            bottom: 36px;
        }

        &__footer-prev {
            font-size: 21px;
        }

        &__footer-next {            
            font-size: 21px;
        }
    }

    
}

@media screen and (max-width: 992px) {
    //=========STEPS============
    .quiz {

        &__content {
            width: 100%;
            max-width: none;
        }

        &__wrap {
            &:after {
                background-color: rgb(18, 18, 17);
                opacity: 0.25;
                transition: 0.3s;
            }

            &:before {
                content: '';
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 240px;
                background: linear-gradient(180deg, rgba(0, 0, 0, 0.79) 0%, rgba(0, 0, 0, 0) 100%);
            }

            &.change {
                transition: 0.3s;
                &:after {
                    opacity: 1;
                    transition: 0.3s;
                }
                
            }
        }

        &__header {        
            transform: translateY(0);
            opacity: 1;
            animation: none;
        }

        &__form-step-title {            
            font-size: 24px;   
        }   

        &__form-label {        
            margin-bottom: 20px;      
        }

        &__form-checkbox {
    
            &:checked + .quiz__form-checkbox-text {
                box-shadow: none;
                text-shadow: none;
                background: rgba(203, 152, 92, 0.43);    
                border: 2px solid #AFAFAF;
    
                &:after {
                    display: none;
                }
                            
            }
        }
    
        &__form-checkbox-text {
            font-size: 18px;
            line-height: 96%;
            border: 2px solid #7A7A7A;            
    
            &:hover {
                box-shadow: none;
                text-shadow: none;
                
    
    
                &:after {
                    display: none;
                }            
            } 
        }
        
        &__footer {            
            width: calc(100% - 90px);
            max-width: none;        
            padding-right: 134px;

            transform: translateY(0);
            opacity: 1;
            animation: none;
        }
    }


    
}

@media screen and (max-width: 768px) {
    //=========STEPS============
    .quiz {
        &__content {
            padding: 0px 35px 120px;
        }

        &__footer {            
            width: calc(100% - 35px);
            max-width: none;        
            padding-right: 35px;
            left: 35px;
        }
    }
}

@media screen and (max-width: 576px) {
//=========STEPS============
    .quiz {

        &__header {
            padding: 36px 0 0;
            
        }

        &__wrap {
            &.last-steps {
                .quiz__content {
                    padding: 0px 35px 110px 35px;
                }
            }
        }        

        &__form-step {
            
    
            &_offer {
                .quiz__form-step-title {
                    margin-bottom: 10px;
                    font-size: 30px;
                    margin-top: 0;
                }
    
                .quiz__form-step-subtitle {
                    margin-bottom: 40px;
                    font-size: 18px;
                }

                .quiz__form-btn {
                    margin-bottom: 0;
                    font-size: 21px;
                }
            }

            &_callback {
                .quiz__form-step-title {
                    margin-bottom: 35px;
                    margin-top: 0;
                    font-size: 28px;
                }
    
                .quiz__form-step-subtitle {
                    margin-bottom: 70px;
                    font-size: 18px;
                }

                .quiz__form-btn {
                    margin-bottom: 36px;
                    font-size: 21px;

                    &:last-of-type {
                        margin-bottom: 0;
                    }
                }
            }
        }

        &__content {
            padding: 0px 35px 190px 35px;
        } 

        &__form-name {
            font-size: 16px;
        }

        &__form-input {
            font-size: 14px;
            padding: 10px 0;
        }

        &__form-submit {
            font-size: 16px;
        }

        &__footer {
            left: 0;
            width: 100%;
            padding: 0 35px;
            bottom: 45px;
        }

        &__footer-btns {            
            display: flex;
            flex-direction: column-reverse;
            
            margin-top: 15px;
            width: 100%;
        }

        &__footer-prev {
            display: block;
            opacity: 0;
            font-size: 21px;
            line-height: 96%;    
            margin-right: 0;
            margin-top: 35px;
    
            &.visible {
                display: block;
                opacity: 1;                
    
                &:after {
                   width: 140px;
                   transition: 0s;
                   transition-delay: 0s;
                   right: 0;
                   left: -35px;
                   margin: auto;
                }
            }
    
            &:after {
               content: '';
               
            }

            &:hover {
                &:after {
                    display: none;
                    
                }
            }          
            
        }
    
        &__footer-next {
            font-size: 21px;
            line-height: 96%;
            background: rgba(217, 217, 217, 0.7);           
            display: block;
            width: 100%;
            max-width: none;        
            
            &:hover {
                background: rgba(217, 217, 217, 0.7);
            }
        
        }
        
    }

}

